<script>
    export default {
        name: "user-view",
        data() {
            return {
                model : {
                    payment : {},
                    transaction : {},
                },
                loading: true
            };
        },
        methods : {
            init(){
                this.loading = true;
                window.axios.get(`${window.location.origin}/admin/airtime/${this.$route.params.id}/view`).then((response) => {
                    this.model = response.data.body.model;
                }).finally(() => {
                    this.loading = false;
                });
            },

            action(action){
                window.action(action , 'Zesa' , `${window.location.origin}/admin/airtime/${this.$route.params.id}/${action}`).then((response) => {
                    this.model = response.data.body.model;
                });
            },
        },
        mounted() {
            this.init();
        }
    }
</script>
<template>
    <div :class="['dimmer' , loading ? 'active' : '']">
        <div class="loader"></div>
        <div class="dimmer-content">
            <div class="row">
                <div class="col-lg-12">
                    <div class="card ribbon-box ">
                        <div class="card-body">
                            <div class="d-flex align-items-center mb-3 flex-wrap">
                                <h4 class="header-title m-0 flex-fill">Airtime Information</h4>
                                <div class="">
                                    <button  @click="init" type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-reload mr-1"></i> Refresh
                                    </button>
                                    <button v-if="!(model.state === 'COMPLETED')" @click="action('abort')" type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-lightbulb-off mr-1"></i> Abort
                                    </button>
                                    <button v-if="!(model.state === 'COMPLETED')" @click="action('retry')" type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-refresh mr-1"></i> Retry Airtime
                                    </button>
                                </div>


                            </div>

                            <div v-if="model.transaction" class="text-left">
                                <p>
                                    <strong class="mr-2">Transaction  :</strong>
                                    <router-link class="badge badge-light p-2 m-1 font-12" :to="`/admin/transactions/${model.transaction.id}/view`">Transaction - #{{ model.transaction.id }}</router-link>
                                </p>
                            </div>

                            <p>
                                <strong class="mr-2">Network :</strong>
                                <span>{{ model.network }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Reply Code :</strong>
                                <span>{{ model.ReplyCode }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Reply Msg :</strong>
                                <span>{{ model.ReplyMsg }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Wallet Balance :</strong>
                                <span>{{ model.WalletBalance }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Discount :</strong>
                                <span>{{ model.Discount }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Initial Balance :</strong>
                                <span>{{ model.InitialBalance }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Final Balance :</strong>
                                <span>{{ model.FinalBalance }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Agent Reference :</strong>
                                <span>{{ model.AgentReference }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Phone :</strong>
                                <span>{{ model.phone }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Recharge ID :</strong>
                                <span>{{ model.RechargeID }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Amount :</strong>
                                <span>{{ model.amount }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Rate :</strong>
                                <span>{{ model.rate }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">State :</strong>
                                <span>{{ model.state }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Rated Amount :</strong>
                                <span>{{ model.rated_amount }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Error :</strong>
                                <span>{{ model.error }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Aborted :</strong>
                                <span>{{ model.abort }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Created :</strong>
                                <span>{{ model.created_at }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Last :</strong>
                                <span>{{ model.updated_at }}</span>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
